"use client";

import { useState, useRef, useEffect } from "react";
import Image from "next/image";

interface ChatMessage {
  id: string;
  question: string;
  response: string;
  category: string;
  timestamp: Date;
  isStreaming?: boolean;
}

interface Category {
  name: string;
  description: string;
}

export default function Home() {
  const [selectedCategory, setSelectedCategory] = useState("crop_info");
  const [question, setQuestion] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [isStreaming, setIsStreaming] = useState(false);
  const [chatHistory, setChatHistory] = useState<ChatMessage[]>([]);
  const [categories, setCategories] = useState<Category[]>([]);
  const [streamingResponse, setStreamingResponse] = useState("");
  const [useStreaming, setUseStreaming] = useState(false);

  const chatEndRef = useRef<HTMLDivElement>(null);
  const questionInputRef = useRef<HTMLInputElement>(null);

  // Fetch categories on component mount
  useEffect(() => {
    fetchCategories();
  }, []);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [chatHistory, streamingResponse]);

  const fetchCategories = async () => {
    try {
      const response = await fetch("http://localhost:8000/agents/categories");
      const data = await response.json();

      // Map categories to our format
      const mappedCategories = data.primary_categories.map((cat: string) => ({
        name: cat,
        description: data.descriptions[cat] || "Agricultural information and advice"
      }));

      setCategories(mappedCategories);
    } catch (error) {
      console.error("Failed to fetch categories:", error);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!question.trim() || isLoading) return;

    const newMessage: ChatMessage = {
      id: Date.now().toString(),
      question: question.trim(),
      response: "",
      category: selectedCategory,
      timestamp: new Date(),
    };

    setChatHistory(prev => [...prev, newMessage]);
    setQuestion("");
    setIsLoading(true);

    if (useStreaming) {
      await handleStreamingChat(newMessage);
    } else {
      await handleRegularChat(newMessage);
    }
  };

  const handleRegularChat = async (message: ChatMessage) => {
    try {
      const response = await fetch("http://localhost:8000/chat", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          category: message.category,
          question: message.question,
          language: "en-US",
        }),
      });

      if (response.ok) {
        const responseText = await response.text();
        updateMessageResponse(message.id, responseText);
      } else {
        updateMessageResponse(message.id, "Sorry, I couldn't process your request. Please try again.");
      }
    } catch (error) {
      updateMessageResponse(message.id, "An error occurred while processing your request.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleStreamingChat = async (message: ChatMessage) => {
    try {
      setIsStreaming(true);
      setStreamingResponse("");

      const response = await fetch("http://localhost:8000/chat/stream", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          category: message.category,
          question: message.question,
          language: "en-US",
        }),
      });

      if (response.ok && response.body) {
        const reader = response.body.getReader();
        const decoder = new TextDecoder();

        while (true) {
          const { done, value } = await reader.read();
          if (done) break;

          const chunk = decoder.decode(value);
          setStreamingResponse(prev => prev + chunk);
        }

        // Update the message with the complete response
        updateMessageResponse(message.id, streamingResponse);
        setStreamingResponse("");
      } else {
        updateMessageResponse(message.id, "Sorry, I couldn't process your request. Please try again.");
      }
    } catch (error) {
      updateMessageResponse(message.id, "An error occurred while processing your request.");
    } finally {
      setIsLoading(false);
      setIsStreaming(false);
    }
  };

  const updateMessageResponse = (messageId: string, response: string) => {
    setChatHistory(prev =>
      prev.map(msg =>
        msg.id === messageId ? { ...msg, response } : msg
      )
    );
  };

  const getCategoryIcon = (category: string) => {
    const icons: { [key: string]: string } = {
      crop_info: "üåæ",
      fertilizers: "üå±",
      market_prices: "üìä",
      gov_schemes: "üèõÔ∏è",
      other: "üîç",
    };
    return icons[category] || "üåæ";
  };

  const getCategoryColor = (category: string) => {
    const colors: { [key: string]: string } = {
      crop_info: "bg-green-100 text-green-800 border-green-200",
      fertilizers: "bg-blue-100 text-blue-800 border-blue-200",
      market_prices: "bg-purple-100 text-purple-800 border-purple-200",
      gov_schemes: "bg-orange-100 text-orange-800 border-orange-200",
      other: "bg-gray-100 text-gray-800 border-gray-200",
    };
    return colors[category] || "bg-gray-100 text-gray-800 border-gray-200";
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 to-blue-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-3">
              <div className="w-10 h-10 rounded-lg flex items-center justify-center">
                <span className="text-white text-xl font-bold">üåæ</span>
              </div>
              <div>
                <h1 className="text-xl font-bold text-gray-900">AgriSaarthi</h1>
                <p className="text-sm text-gray-500">AI-Powered Agricultural Assistant</p>
              </div>
            </div>

            {/* Toggle for streaming vs regular chat */}
            <div className="flex items-center space-x-2">
              <span className="text-sm text-gray-600">Streaming:</span>
              <button
                onClick={() => setUseStreaming(!useStreaming)}
                className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${useStreaming ? 'bg-green-600' : 'bg-gray-200'
                  }`}
              >
                <span
                  className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${useStreaming ? 'translate-x-6' : 'translate-x-1'
                    }`}
                />
              </button>
            </div>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          {/* Sidebar - Categories */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Categories</h2>
              <div className="space-y-3">
                {categories.map((category) => (
                  <button
                    key={category.name}
                    onClick={() => setSelectedCategory(category.name)}
                    className={`w-full text-left p-3 rounded-lg border transition-all duration-200 ${selectedCategory === category.name
                      ? "border-green-500 bg-green-50 text-green-800"
                      : "border-gray-200 hover:border-gray-300 hover:bg-gray-50"
                      }`}
                  >
                    <div className="flex items-center space-x-3">
                      <span className="text-xl">{getCategoryIcon(category.name)}</span>
                      <div>
                        <div className="font-medium capitalize">
                          {category.name.replace(/_/g, " ")}
                        </div>
                        <div className="text-sm text-gray-500 line-clamp-2">
                          {category.description}
                        </div>
                      </div>
                    </div>
                  </button>
                ))}
              </div>
            </div>
          </div>

          {/* Main Chat Area */}
          <div className="lg:col-span-3">
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 flex flex-col h-[600px]">
              {/* Chat Header */}
              <div className="p-4 border-b border-gray-200">
                <div className="flex items-center space-x-3">
                  <span className="text-2xl">{getCategoryIcon(selectedCategory)}</span>
                  <div>
                    <h3 className="font-semibold text-gray-900 capitalize">
                      {selectedCategory.replace(/_/g, " ")}
                    </h3>
                    <p className="text-sm text-gray-500">
                      {categories.find(c => c.name === selectedCategory)?.description}
                    </p>
                  </div>
                </div>
              </div>

              {/* Chat Messages */}
              <div className="flex-1 overflow-y-auto p-4 space-y-4">
                {chatHistory.length === 0 ? (
                  <div className="text-center py-12">
                    <div className="text-6xl mb-4">üåæ</div>
                    <h3 className="text-lg font-medium text-gray-900 mb-2">
                      Welcome to AgriSaarthi!
                    </h3>
                    <p className="text-gray-500">
                      Ask me anything about agriculture, farming, or related topics.
                    </p>
                  </div>
                ) : (
                  chatHistory.map((message) => (
                    <div key={message.id} className="space-y-3">
                      {/* User Question */}
                      <div className="flex justify-end">
                        <div className="max-w-[80%] bg-blue-600 text-white p-3 rounded-lg rounded-br-none">
                          <p className="text-sm">{message.question}</p>
                          <div className="flex items-center justify-between mt-2 text-xs opacity-75">
                            <span className="capitalize">{message.category.replace(/_/g, " ")}</span>
                            <span>{message.timestamp.toLocaleTimeString()}</span>
                          </div>
                        </div>
                      </div>

                      {/* AI Response */}
                      <div className="flex justify-start">
                        <div className="max-w-[80%] bg-gray-100 text-gray-900 p-3 rounded-lg rounded-bl-none">
                          {message.response ? (
                            <div>
                              <p className="text-sm whitespace-pre-wrap">{message.response}</p>
                              <div className="flex items-center justify-between mt-2 text-xs text-gray-500">
                                <span className="capitalize">{message.category.replace(/_/g, " ")}</span>
                                <span>{message.timestamp.toLocaleTimeString()}</span>
                              </div>
                            </div>
                          ) : (
                            <div className="flex items-center space-x-2">
                              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-green-600"></div>
                              <span className="text-sm text-gray-500">Thinking...</span>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  ))
                )}

                {/* Streaming Response */}
                {isStreaming && streamingResponse && (
                  <div className="flex justify-start">
                    <div className="max-w-[80%] bg-gray-100 text-gray-900 p-3 rounded-lg rounded-bl-none">
                      <p className="text-sm whitespace-pre-wrap">{streamingResponse}</p>
                      <div className="flex items-center justify-between mt-2 text-xs text-gray-500">
                        <span className="capitalize">{selectedCategory.replace(/_/g, " ")}</span>
                        <span>Streaming...</span>
                      </div>
                    </div>
                  </div>
                )}

                <div ref={chatEndRef} />
              </div>

              {/* Chat Input */}
              <div className="p-4 border-t border-gray-200">
                <form onSubmit={handleSubmit} className="flex space-x-3">
                  <input
                    ref={questionInputRef}
                    type="text"
                    value={question}
                    onChange={(e) => setQuestion(e.target.value)}
                    placeholder={`Ask about ${selectedCategory.replace(/_/g, " ")}...`}
                    className="flex-1 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent outline-none"
                    disabled={isLoading}
                  />
                  <button
                    type="submit"
                    disabled={!question.trim() || isLoading}
                    className="px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 focus:ring-2 focus:ring-green-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                  >
                    {isLoading ? (
                      <div className="flex items-center space-x-2">
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                        <span>Ask</span>
                      </div>
                    ) : (
                      "Ask"
                    )}
                  </button>
                </form>

                {/* Streaming Toggle Info */}
                <div className="mt-2 text-xs text-gray-500 text-center">
                  {useStreaming ? (
                    <span className="text-green-600">üîÑ Streaming responses enabled</span>
                  ) : (
                    <span>üìù Regular responses enabled</span>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
